@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@700&family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0f1014;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Home {
    background-color: #0f1014;
    padding: 80px 160px;
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin: auto;
}

h1, h2 {
    font-family: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
    color: #e5e9f0;
    font-size: 28px;
    line-height: 120%;
    margin: 0;
    padding: 0;
}

p, button, h3, a {
    font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    color: #e5e9f0;
    font-size: 21px;
    line-height: 150%;
    margin: 0;
    padding: 0;
}

h2 {
    font-weight: 700;
}

a {
    text-decoration: none;
    color: #88c0d0;
    font-family: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
}

a:visited {
    text-decoration: none;
    color: #88c0d0;
}

#secondary {
    color:#969db7;
}

#header {
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
    letter-spacing: 4px;
    color: #969db7;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    padding-bottom: 32px;
}

span {
    text-decoration: underline;
}

#highlight {
    color: #88c0d0;
}

.fade-in-section {
    opacity: 0;
    visibility: hidden;
    transition: opacity 600ms ease-in, visibility 600ms ease-out;
    will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
    opacity: 1;
    visibility: visible;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Home {
        padding: 48px 24px;
        overflow-x: hidden;
    }

    h1 {
        font-size: 21px;
    }

    h2, p, button, h3, a {
        font-size: 18px;
    }

    #header {
        padding-bottom: 24px;
    }
}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Home {
        padding: 80px 80px;
    }

}
.Hero {
    width: 500px;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;
}

#strikethrough {
    text-decoration: line-through;
}

.Title {
    display: flex;
    flex-direction: column;
    height: 48px;
    padding-bottom: 32px;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.Socials {
    display: flex;
    flex-direction: row;
    width: 90px;
    justify-content: space-between;
    height: 20px;
    padding-bottom: 32px;
}

.Bio {
    padding-bottom: 32px;
}

.Socials a img {
    height: 20px;
    opacity: 0.4;
    transition: 0.3s ease-in-out;
    transform: translate();
}

.Socials a img:hover {
    opacity: 1;
    transform: translateY(-1px) scale(1.05);
} 

/* mobile portrait */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {

    .Hero {
        padding: 32px 0px;
        width: 100%;
    }

    .Bio {
        width: 100%;
    }

    .Title, .Socials .Bio {
        padding-bottom:24px;
    }
    
}
.Button {
    padding: 8px 16px;
    background-color: #181d26;
    border-radius: 4px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transform: translate();
    transition: 300ms ease-in-out;
}

.Button:hover {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0px 4px 8px rgba(10, 11, 12, 0.6);
}
.hover_img  { 
    position:relative;
}

.hover_img button {
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
}

.hover_img span {
    position:absolute;
    display:none; 
    z-index:99; 
    margin-top:-100px; 
    margin-left: -50px; 
    left: 0; 
    top: 0; 
    opacity: 0.7;
}

.hover_img span img {
    width: 200px; 
    height: auto;
}

.hover_img:hover span { 
    display:block; 
}

button {
    border:none;
    background-color: transparent;
    color: #88c0d0;
}
.Projects {
    padding: 32px 24px;
    margin-bottom: 32px;
}


/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .Projects {
        padding: 32px 0px;
        margin-bottom: 12px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) {
    .Projects {
        padding: 32px 24px;
    }
}
.Post {
    padding-bottom: 128px;
}

.Post > img {
    width: 100%;
    height: 500px;
    object-fit: cover;
    object-position: top;
    border-radius: 12px;
    margin-bottom: 32px;
    transform: translate();
    transition: 300ms ease-in-out;
}

.Post:hover > img {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0px 24px 48px rgba(10, 11, 12, 0.6);
}

.ContentContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 16px;
}

.ContentContainer .RightPane {
    width: 500px;
}

.ContentContainer .LeftPane {
    width: 450px;
}

.ContentContainer .LeftPane h1 {
    margin-bottom: 12px;
}

.Logos {
    padding-top: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.publication {
    height: 18px;
    margin-right: 18px;
    width: auto;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .Post {
        padding-bottom: 64px;
    }

    .Post > img {
        width: 100%;
        height: 200px;
    }

    .ContentContainer {
        flex-direction: column;
        padding: 0px;
    }

    .ContentContainer .RightPane, .ContentContainer .LeftPane {
        width: 100%;
    }

    .Logos {
        padding-top: 12px;
    }

    .ContentContainer .RightPane {
        margin-top: 12px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {

    .Post > img {
        height: 400px;
    }
        
    .ContentContainer {
        width: 500px
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {

    .Post > img {
        height: 300px;
        margin-bottom: 16px;
    }

}
.TestimonialContainer {
    display:flex;
    flex-direction: row;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    margin-bottom: 32px;
}

.Testimonial {
    padding: 24px 32px;
}

.TestimonialPost {
    margin-right: 32px;
    width: 400px;
    padding: 24px 32px;
    border-radius: 8px;
    transform: translate();
    transition: 300ms ease-in-out;
    background-color: #181d26;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.TestimonialPost #secondary {
    color:#969db7;
    text-decoration: none;
}

.TestimonialPost #focus {
    color: white;
    text-decoration: none;
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
}

.TestimonialPost:hover {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0px 12px 24px rgba(10, 11, 12, 0.6);
}

.TestimonialPost .Author {
    margin-top: 24px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.TestimonialPost .Author a {
    font-weight: 600;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .TestimonialContainer {
        flex-direction: column;
        margin-bottom: 12px;
    }

    .Testimonial {
        padding: 32px 0px;
    }

    .TestimonialPost {
        margin-top: 16px;
        margin-right: 0px;
        width: auto;
        padding: 16px 24px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialPost {
        width: 500px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialContainer {
        max-width: 500px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}
.Contact {
    width: 500px;
    padding: 0px 24px;
}

.Contact #text {
    margin-bottom: 32px;
}

.ButtonContainer {
    display: flex;
    margin-bottom: 32px;
    flex-direction: row;
}

.ButtonContainer div {
    margin-right: 24px;
}

@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Contact {
        width: 100%;
        padding: 0px 0px;
    }

    .Contact #text {
        margin-bottom: 12px;
    }

    .ButtonContainer {
        flex-direction: row;
    }

    .ButtonContainer div {
        margin-right: 24px;
        margin-top: 24px;
    }

}
.Skills {
    width: 500px;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;
    margin-top: -96px;
}

.Skills #focus {
    color: white;
    text-decoration: none;
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
}

@media (min-device-width : 320px) and (max-device-width : 480px) {

    .Skills {
        padding: 32px 0px 0px 0px;
        width: 100%;
        margin-bottom: 24px;
    }
    
}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Skills {
        padding: 32px 24px;
        margin-top: -64px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
        
    .Skills {
        padding: 32px 24px;
        margin-top: -64px;
    }
    
}
