.TestimonialContainer {
    display:flex;
    flex-direction: row;
    height:fit-content;
    margin-bottom: 32px;
}

.Testimonial {
    padding: 24px 32px;
}

.TestimonialPost {
    margin-right: 32px;
    width: 400px;
    padding: 24px 32px;
    border-radius: 8px;
    transform: translate();
    -webkit-transition: 300ms ease-in-out;
    -moz-transition: 300ms ease-in-out;
    -o-transition: 300ms ease-in-out;
    transition: 300ms ease-in-out;
    background-color: #181d26;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.TestimonialPost #secondary {
    color:#969db7;
    text-decoration: none;
}

.TestimonialPost #focus {
    color: white;
    text-decoration: none;
    font-family: "Space Grotesk", sans-serif;
    font-weight: 700;
}

.TestimonialPost:hover {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0px 12px 24px rgba(10, 11, 12, 0.6);
}

.TestimonialPost .Author {
    margin-top: 24px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.TestimonialPost .Author a {
    font-weight: 600;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .TestimonialContainer {
        flex-direction: column;
        margin-bottom: 12px;
    }

    .Testimonial {
        padding: 32px 0px;
    }

    .TestimonialPost {
        margin-top: 16px;
        margin-right: 0px;
        width: auto;
        padding: 16px 24px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialPost {
        width: 500px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
        
    .Testimonial {
        padding: 32px 24px;
        /* margin-top: -64px; */
    }

    .TestimonialContainer {
        max-width: 500px;
    }

    .TestimonialPost:hover {
        transform: none;
    }

}