.Hero {
    width: 500px;
    display: flex;
    flex-direction: column;
    padding: 32px 24px;
}

#strikethrough {
    text-decoration: line-through;
}

.Title {
    display: flex;
    flex-direction: column;
    height: 48px;
    padding-bottom: 32px;
    width: max-content;
}

.Socials {
    display: flex;
    flex-direction: row;
    width: 90px;
    justify-content: space-between;
    height: 20px;
    padding-bottom: 32px;
}

.Bio {
    padding-bottom: 32px;
}

.Socials a img {
    height: 20px;
    opacity: 0.4;
    -webkit-transition: 0.3s ease-in-out;
    -moz-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    transform: translate();
}

.Socials a img:hover {
    opacity: 1;
    transform: translateY(-1px) scale(1.05);
} 

/* mobile portrait */
@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {

    .Hero {
        padding: 32px 0px;
        width: 100%;
    }

    .Bio {
        width: 100%;
    }

    .Title, .Socials .Bio {
        padding-bottom:24px;
    }
    
}