.Post {
    padding-bottom: 128px;
}

.Post > img {
    width: 100%;
    height: 500px;
    object-fit: cover;
    object-position: top;
    border-radius: 12px;
    margin-bottom: 32px;
    transform: translate();
    -webkit-transition: 300ms ease-in-out;
    -moz-transition: 300ms ease-in-out;
    -o-transition: 300ms ease-in-out;
    transition: 300ms ease-in-out;
}

.Post:hover > img {
    transform: translateY(-4px) scale(1.05);
    box-shadow: 0px 24px 48px rgba(10, 11, 12, 0.6);
}

.ContentContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 16px;
}

.ContentContainer .RightPane {
    width: 500px;
}

.ContentContainer .LeftPane {
    width: 450px;
}

.ContentContainer .LeftPane h1 {
    margin-bottom: 12px;
}

.Logos {
    padding-top: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.publication {
    height: 18px;
    margin-right: 18px;
    width: auto;
}

/* mobile portrait */
@media (min-device-width : 320px) and (max-device-width : 1024px) {

    .Post {
        padding-bottom: 64px;
    }

    .Post > img {
        width: 100%;
        height: 200px;
    }

    .ContentContainer {
        flex-direction: column;
        padding: 0px;
    }

    .ContentContainer .RightPane, .ContentContainer .LeftPane {
        width: 100%;
    }

    .Logos {
        padding-top: 12px;
    }

    .ContentContainer .RightPane {
        margin-top: 12px;
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {

    .Post > img {
        height: 400px;
    }
        
    .ContentContainer {
        width: 500px
    }

}

@media (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : portrait) {

    .Post > img {
        height: 300px;
        margin-bottom: 16px;
    }

}